<template id="com-dialog" >

  <div class="send-message">
           <el-dialog
          title=""
          :visible.sync="dialogVisible"
          width="30%"
          :before-close="handleClose">
          <div>
              <b>:</b>
              <div>(,)</div>

              <div>1.;
              ;</div>
              <div>2,
            ,
              ,
              ,,
              ,
              
              </div>
              <div>322:007:00,</div>
          </div>
          <span slot="footer" class="dialog-footer">
 
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="dialogVisible = false"> </el-button>
          </span>
        </el-dialog>
        <el-card class="box-card" style="margin-top:10px;">
          <div slot="header" class="clearfix">
            <span></span>
          </div>
          <div class="scope">
            <el-row style="margin-bottom:20px;">
                    
                      <el-select v-model="form.province" placeholder="" @change="selectProvince()">
                        <el-option
                          v-for="item in provinceList"
                          :key="item.code"
                          :label="item.name"
                          :value="item.code">
                        </el-option>
                      </el-select>     
                   
                  
                        <el-select v-model="form.city" @change="selectcity()" placeholder="">
                        <el-option
                          v-for="item in cityList"
                          :key="item.code"
                          :label="item.name"
                          :value="item.code">
                        </el-option>
                      </el-select> 
                                   
                
                      <el-select v-model="form.area" placeholder="">
                        <el-option
                          v-for="item in areaList"
                          :key="item.code"
                          :label="item.name"
                          :value="item.code">
                        </el-option>
                      </el-select> 
                 
            </el-row>
               <el-row>  
                <el-col>
                    <el-select v-model="client" placeholder="" >
                      <el-option
                        v-for="item in clientList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>     
                 </el-col>
                 <el-col> <el-button type="primary" @click="labelOpen = true"></el-button></el-col>  
                 </el-row>
                 <el-row style="margin-top:20px;">
                      <el-tag style="margin:3px;"
                        v-for="tag in selectBox"
                        :key="tag.labelId"
                        >
                        {{tag.labelName}}
                      </el-tag>
                 </el-row>
           
                <el-row style="margin-top:20px;">
                <el-button  type="primary"></el-button>
              </el-row>
          </div> 
        </el-card>
        <el-card class="box-card" style="margin-top:10px;">
          <div slot="header" class="clearfix">
            <span></span> 
              <el-select v-model="modeValue" @change="selectMode" placeholder="">
                <el-option
                  v-for="item in modeList"
                  :key="item.id"
                  :label="item.title"
                  :value="item.id">
                </el-option>
              </el-select>
           
          </div>
          <div class="main">
           <span>:{{smsNumber}}</span>
             <el-row style="margin-top:10px;">
                <el-input
                    type="textarea"
                    :autosize="{ minRows: 4, maxRows: 100}"
                    placeholder=""
                    v-model="modeText"
                    readonly="readonly">
                </el-input>  
             </el-row> 
              <!-- <el-row style="font-size:12px; color:#999; padding-top:15px;">{{modeLength}}</el-row> -->
            </div>     
            <div class="sendBtn">
              <el-button  type="primary" :loading="loading"></el-button>
              <el-button  type="primary" :loading="loading">push</el-button>
            </div>     
        </el-card> 
     
        <el-dialog
        title=""
        :visible.sync="labelOpen"
        width="500px"
        :before-close="labelClose">
            <el-collapse v-model="activeNames" >
              <el-checkbox-group v-model="checkList"> 
              <el-collapse-item  v-for="(item,index) in typeList" :key="index" :name="index" :title="item.name" >
                    <span v-for="(data,indexs) in item.list" :key ="indexs" style="padding: 0 10px;">
                      <el-checkbox :label="data.labelName" :key="data.labelId" @change="selectBoxs(data)" ></el-checkbox>
                    </span> 
              </el-collapse-item>
                </el-checkbox-group> 
            </el-collapse>
        <span slot="footer" class="dialog-footer">
          <el-button @click="labelClose"> </el-button>
          <el-button type="primary" @click="labelOpen = false"> </el-button>
        </span>
      </el-dialog>
  </div>
</template>
<style lang="less">
.send-message {
  .operation {
    width: 100%;
  }
  .operation > span {
    font-size: 14px;
  }
  .operation > .el-input {
    width: 300px;
  }
  .operation > .el-button {
    margin-left: 20px;
  }
  .box-card:last-child {
    margin-top: 20px;
  }
  .sendBtn .el-button {
    float: left;
  }
  .sendBtn {
    width: 360px;
    margin: 0 auto;
    padding: 10px 0;
    padding: 10px 0 0 20px;
    overflow: hidden;
  }
  // .scope .el-select{
  //   margin-left: 20px;
  // }
  .scope .el-col {
    width: auto;
    float: left;
    margin-right: 20px;
  }
}
</style>

<script>
export default {
  // components: {
  //   comDialog
  // },
  data() {
    return {
      tags: [
        { name: "", type: "" },
        { name: "", type: "success" },
        { name: "", type: "info" },
        { name: "", type: "warning" },
        { name: "", type: "danger" }
      ],
      dialogVisible: false,
      loading: false,
      memberPhone: "",
      PhoneList: "",
      switchValue: true,
      numberPhone: 0,
      modeValue: "",
      smsNumber: 0,
      modeText: "",
      modeLength: 60,
      form: {},
      options: [],
      labelOpen: false,
      client: "",
      clientList: [
        {
          label: "IOS",
          value: "IOS"
        },
        {
          label: "",
          value: ""
        },
        {
          label: "",
          value: ""
        }
      ],
      memberType: "",
      memberTypeList: [
        {
          label: "",
          value: "1"
        },
        {
          label: "",
          value: "0"
        }
      ],
      activeNames: 1,
      typeList: [],
      checkList: [],
      modeList: [],
      selectBox: [],
      form: { area: "" },
      provinceList: [],
      cityList: [],
      areaList: []
    };
  },
  methods: {

    addphoneList() {
      let isMobile = /^1([35678][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/;
      if (isMobile.test(this.memberPhone)) {
        let arr = this.PhoneList.split(",");
        arr.map((item, index) => {
          if (item == "") {
            arr.splice(index, index + 1);
          }
        });
        arr.push(this.memberPhone);
        this.PhoneList = arr.toString();
        this.memberPhone = "";
      } else {
        this.$message.error("");
      }
    },
    selectMode() {
      this.modeList.map(item => {
        if (item.id == this.modeValue) {
          this.modeText = item.memo;
        }
      });
    },
    handleClose() {
      this.dialogVisible = false;
    },
    labelClose() {
      this.labelOpen = false;
      // this.checkList = [];
      // this.activeNames = [];
    },
    selectBoxs(data) {
      let no = false;
      let indexs = 0;
      this.selectBox.map((item, index) => {
        if (item.labelName == data.labelName) {
          no = true;
          indexs = index;
        }
      });
      if (no) {
        this.selectBox.splice(indexs, 1);
      } else {
        this.selectBox.push(data);
      }
    },
    getProvince() {
      this.axios
        .post("linkage/getAllProvince", {})
        .then(res => {
          this.provinceList = res.data.result;
        })
        .catch(error => {
          //
        });
    },
    selectProvince() {
      let provinceCode = this.form.province;
      this.axios
        .post("linkage/getCityByProvince", { provinceCode })
        .then(res => {
          this.cityList = res.data.result;
          this.form.city = this.cityList[0].code;
          this.selectcity();
        })
        .catch(error => {
          //
        });
    },
    selectcity() {
      let cityCode = this.form.city;
      this.axios
        .post("linkage/getAreaByCity", { cityCode })
        .then(res => {
          this.areaList = res.data.result;
          this.form.area = this.areaList[0].code;
        })
        .catch(error => {
          //
        });
    }
  },

  mounted() {
    this.getProvince();
    this.axios
      .post("/labelEditing/tabulationLabel", {})
      .then(res => {
        //this.typeList = res.data.result;
        //  this.typeList.map(item=>{

        //           this.axios.post('/labelEditing/initLabel', { labelTypeId: item.typeId }).then(res => {
        //             item.arr = res.data.result;
        //           }).catch(error => { //
        //           })

        // })
        let data = res.data.result;
        // for(let i = 0; i<data.length; i++){
        //   console.log(data[i]);
        // }
        for (var o in data) {
          let json = {};
          json.name = o;
          json.list = data[o];
          this.typeList.push(json);
        }
        console.log(this.typeList);
      })
      .catch(error => {
        //
      });

    this.axios
      .post("/smsTemplate/selectSmsTemplate", {})
      .then(res => {
        this.modeList = res.data.result;
      })
      .catch(error => {
        //
      });
  },
  //
  watch: {
    "$route.params.id": "getData",
    PhoneList(val) {
      let arr = this.PhoneList.split(",");
      arr.map((item, index) => {
        if (item == "") {
          arr.splice(index, index + 1);
        }
      });
      this.numberPhone = arr.length;
    },
    modeText(val) {
      this.modeLength = 60 - val.length;
    }

    // checkList(){
    //   console.log(this.checkList)
    // }
  }
};
</script>



