<template id="com-dialog" >

  <div class="task-detail">
    <div class="quote-title">  {{ detailData.sendee }}</div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <div class="main">
       <el-row>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.shopName}}</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.shopCode}}</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.shortName}}</div></el-col>
      </el-row>
       <el-row>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.type == 0 ? '':(detailData.type == 1?'':'' ) }}</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.level}}</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.shopType}}</div></el-col>
      </el-row>    
       <el-row>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.shopBrand}}</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.shopAddress}}</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.shopArea}}</div></el-col>
      </el-row>      
       <el-row>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.electricityPrice}}</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.waterPrice}}</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.housePrice}}</div></el-col>
      </el-row> 
       <el-row>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.managerNum}}</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.employeePrice}}</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.otherPrice}}</div></el-col>
      </el-row>   
       <el-row>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.boilerType}}</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.openDate}}</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.probationDate}}</div></el-col>
      </el-row>
       <el-row>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.expirationDate}}</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.shopTel}}</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">{{detailData.businessLicenseNum}}</div></el-col>
      </el-row>   
       <el-row>
          <el-col :span="8"><div class="grid-content bg-purple">{{detailData.province}}</div></el-col>
          <el-col :span="8"><div class="grid-content bg-purple">{{detailData.city}}</div></el-col>
          <el-col :span="8"><div class="grid-content bg-purple">{{detailData.area}}</div></el-col>       
      </el-row>
                           
      </div>
    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <div class="main">
           <template>
              <el-table
                :data="memberList"
                style="width: 100%">
                <el-table-column
                  prop="name"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="mobilePhone"
                  label="">
                </el-table-column>
                <el-table-column
                  label="">
                    <template slot-scope="scope">
                        {{scope.row.babyNumber == 0||scope.row.babyNumber == 1?'':(scope.row.babyNumber == 2?'':(scope.row.babyNumber == 3?'':scope.row.babyNumber+''))}}
                    </template>
                </el-table-column>
                <el-table-column
                  prop="source"
                  label="">
                </el-table-column>
                <el-table-column
                  label="">
                    <template slot-scope="scope">
                        {{scope.row.havaCard == 0 ? '': ''}}
                    </template>                  
                </el-table-column>
                <el-table-column
                  prop="address"
                  label="">
                </el-table-column>      
                <el-table-column
                  prop="loginDate"
                  label="APP">
                </el-table-column> 
                <el-table-column
                  prop="reserveDate"
                  label="">
                </el-table-column>                                                                                           
              </el-table>
            </template>
      </div>
      <div class="pagination"><el-pagination background layout="prev, pager, next" :total="memberTotal" @current-change="memberChange" :current-page.sync="memberPageNum"></el-pagination></div>
    </el-card>  

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <div class="main">
           <template>
              <el-table
                :data="memberInsiderList"
                style="width: 100%">
                          <el-table-column
                  prop="name"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="mobilePhone"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="babyNumber"
                  label="">
                    <template slot-scope="scope">
                        {{scope.row.babyNumber == 0||scope.row.babyNumber == 1?'':(scope.row.babyNumber == 2?'':(scope.row.babyNumber == 3?'':scope.row.babyNumber+''))}}
                    </template>                  
                </el-table-column>
                <el-table-column
                  prop="source"
                  label="">
                </el-table-column>
                <el-table-column
                  label="">
                    <template slot-scope="scope">
                        {{scope.row.havaCard == 0 ? '': ''}}
                    </template>                   
                </el-table-column>
                <el-table-column
                  prop="address"
                  label="">
                </el-table-column>      
                <el-table-column
                  prop="loginDate"
                  label="APP">
                </el-table-column> 
                <el-table-column
                  prop="reserveDate"
                  label="">
                </el-table-column>                                                                                           
              </el-table>
            </template>
      </div>
      <div class="pagination"><el-pagination background layout="prev, pager, next" :total="memberInsiderTotal" @current-change="memberInsiderChange" :current-page.sync="memberInsiderPageNum"></el-pagination></div>      
    </el-card> 
  </div>
</template>
<style lang="less">
.task-detail{
  .main .el-row{
    padding-bottom: 20px;
  }
  .main:first-child{
    font-size: 14px;
  }
  .quote-title {
    margin-bottom: 20px;
    padding: 12px 15px;
    line-height: 22px;
    border-left: solid 5px #00a2ca;
    border-radius: 0 2px 2px 0;
    background-color: #f2f2f2;
  }
 .box-card{
   margin-top: 30px;
 }
 .box-card:last-child{
   margin-bottom: 200px;
 }
 .bq_main{
   margin-bottom: 40px;
 }
 .bq_noList{
   color: #ccc;
   font-size: 14px;
   text-align: center; 

    }
.el-tag{ 
  float: left;
  margin-left: 10px;
  }
  .main .el-col{
    padding: 0 5px;
  }
  .pagination{
    margin-top: 20px;
    text-align: right;
  }
}
</style>

<script>
export default {
  // components: {
  //   comDialog
  // },
  data() {
    return {
        detailData:{},
        memberTotal:1,
        memberPageNum:1,
        memberInsiderTotal:1,
        memberInsiderPageNum: 1,
        tableData: [],
        memberList: [],
        memberInsiderList:[]
    };
  },
  methods: {
    //
    getData(){
      let id = this.$route.params.id;
      let arrNum = ['','','','','',''];
      this.axios.post('/store/storeDetails', { storeId: id }).then(res => {
        if(res.data.result.store.length){ this.detailData = res.data.result.store[0] };
        this.memberList = res.data.result.member; 
        this.memberList.map(item=>{
          item.babyNumber  =  arrNum[item.babyNumber-1] + '';
          item.havaCard  =  item.havaCard == 0 ? item.havaCard = '' : '';
        })
        this.memberInsiderList = res.data.result.memberInsider;
        this.memberInsiderList.map(item=>{
          item.babyNumber  =  arrNum[item.babyNumber-1] + '';
          item.havaCard  =  item.havaCard == 0 ? item.havaCard = '' : '';
        })
      }).catch(error => { //
      })
 
    },
    getstoreMember(){
      let id = this.$route.params.id;
        this.axios.post('/store/storeMember', { storeId: id, pageNum: this.memberPageNum }).then(res => {
        this.memberTotal = res.data.result.countMember;
        this.memberList = res.data.result.member; 
        this.memberList.map(item=>{
          item.babyNumber  =  arrNum[item.babyNumber-1] + '';
          item.havaCard  =  item.havaCard == 0 ? item.havaCard = '' : '';
        })
      }).catch(error => { //
      })
    },
    memberChange(val){
      this.memberPageNum= val;
      this.getstoreMember();
    },
    getstoreMemberInsider(){
        let id = this.$route.params.id;
        this.axios.post('/store/storeMemberInsider', { storeId: id, pageNum: this.memberInsiderPageNum  }).then(res => {
        this.memberInsiderTotal = res.data.result.countMemberInsider;
        this.memberInsiderList = res.data.result.memberInsider; 
        this.memberInsiderList.map(item=>{
          item.babyNumber  =  arrNum[item.babyNumber-1] + '';
          item.havaCard  =  item.havaCard == 0 ? item.havaCard = '' : '';
        })
      }).catch(error => { //
      })      
    },
    memberInsiderChange(val){
      this.memberInsiderPageNum= val;
      this.getstoreMemberInsider();
    },
    
  },

  mounted(){
    this.getData(); 
    this.getstoreMember();
    this.getstoreMemberInsider();
  },
  //
  watch:{
    "$route.params.id": "getData"
  },

};
</script>



